	module.exports = {"data":{"friendsJson":{"name":"Whitey","description":"<p>Whitey is a seven foot dolphin with white on her dorsal and on the right end of her fluke. The left side of her fluke has a small snip off the end and both sides are ragged. Both fins are also ragged. I met her Dec 26th 2013 she appeared to know me. She didn't want anything to do with tour boats and left immediately upon their arrival. This happened on both encounters that day. I don't actually know the sex and would guess maybe female because Whitey was visiting Julia and Frisky. She clearly came from outside the reef when we saw her the second time.</p>","featuredImage":{"src":{"publicURL":"/static/friend-pic-whitey-c1857de4c9963b456fd65093fda63d60.jpg"},"caption":null},"images":[{"src":{"publicURL":"/static/whitey-97fb2d2a969f9e1032530ab9a976094b.jpg"},"caption":"Whitey's distinctive dorsal."},{"src":{"publicURL":"/static/whitey2-e4bba4c47c5940cfadbf569628ac8249.jpg"},"caption":"JoJo and Whitey swimming together."}],"videos":[{"src":"//www.youtube.com/embed/10KdpT6UGdw?rel=0&fs=1&showinfo=1","source":"youtube"}]}},"pathContext":{}}

